<template>
  <div class="venue_details">
    <venue-head title="详情" down="" :booler="object.past"></venue-head>
    <div class="applicat-title">{{applicaTitle}}的预约申请</div>
    <ul class="applicatLists">
      <li v-for="(item, index) in details" :key="index">
        <span class="name">{{item.name}}</span>：
        <span class="key">{{object[item.key]}}</span>
      </li>
    </ul>
    <div class="feedback-title">{{feedbackTitle}}</div>
    <ul class="feedbackLists">
      <template v-if='object.past'>
        <li v-if="object.status !== 'n'">
          <span class="name">审核状态</span>
          <span class="key" v-if="object.status == 'o'" style="color: #1f95ff">通过</span>
          <span class="key" v-if="object.status == 'y'" style="color: #1f95ff">通过</span>
          <span class="key" v-if="object.status == 'n'" style="color: red">拒绝</span>
          <span class="key" v-if="object.status == 'q'" style="color: red">已取消</span>
        </li>
        <li>
          <span class="name">意见反馈</span>
          <span class="key">同意使用， 请使用完毕后物品放回原位</span>
        </li>
      </template>
      <template v-else>
        <li>
          <span class="name">审核状态</span>
          <span class="key" v-if="object.status == 'o'">待审核</span>
          <span class="key" v-if="object.status == 'd'">待审核</span>
          <span class="key" style="color: red" v-if="object.status == 'q'">已取消</span>
        </li>
        <li v-if="object.status !== 'q'">
          <span class="name">意见反馈</span>
          <span class="key textarea"><textarea placeholder="同意使用， 请使用完毕后物品放回原位" v-model="Feedback" name="" id="" cols="auto" rows="auto"></textarea></span>
        </li>
      </template>
    </ul>
    <template v-if='footerBl'>
      <div class="footerDetails">
        <a href="javascript:;" v-if='cancel' class="refuseDetails" @click="cancelAppmt(object.fatherId||object.id)">取消</a>
        <a href="javascript:;" v-if='cancels' class="refuseDetails" @click="byRejecting(object.fatherId||object.id, false)">拒绝</a>
        <a href="javascript:;" v-if='cancels' class="byDetails" @click="byRejecting(object.fatherId||object.id, true)">通过</a>
      </div>
    </template>
  </div>
</template>
<script>
import VenueHead from './Head.vue'
import axios from 'axios'
import { setTimeout } from 'timers';
export default {
  data () {
    return {
      applicaTitle: "",
      feedbackTitle: '申请反馈',
      Feedback: '',
      details: [
        {name: '预约场馆', key: 'roomName'},
        {name: '申请人', key: 'publisher'},
        {name: '电话', key: 'phone'},
        {name: '开始时间', key: 'startTime'},
        {name: '使用时长', key: 'toFixed'},
        {name: '场馆地址', key: 'address'}
      ],
      object: {},
      status: '',
      footerName: [
        {name:'拒绝', key: '', booler: false, className: 'refuseDetails'},
        {name:'通过', key: '', booler: true, className: 'byDetails'},
        {name:'取消', key: '', booler: true, className: 'cancel cancelDetails refuseDetails'}
      ],
      typleNamesState: false,
      footerBl: false,
      cancel: false,
      cancels: false
    }
  },
  created () {
    localStorage.typleNames
    this.detailsList()
  },
  methods: {
    detailsList () {
      const typleNames = localStorage.typleNames
      if(typleNames == "预约已审"){
        this.typleNamesState = true
      }
      const times = this.$route.params.item
      if(times){
        if((typleNames == "预约已审"&&times.status == 'y')||(typleNames == "预约待审"&&times.status == 'd')) {
          this.footerBl = this.cancel =  true
        }
        if(typleNames == "审核待审") {
          this.footerBl = this.cancels =  true
        }
        if(times.appBoardroomOrder) {
          times.appBoardroomOrder.fatherId = times.id
        }
        const data = times.appBoardroomOrder||times
        this.object = data
        data.roomName = data.appBoardroomDetail.roomName
        data.address = data.appBoardroomDetail.address
        data.toFixed = this.toFixed(data.startTime, data.deadLine)
        this.applicaTitle = data.publisher
        this.status = data.status
      }
    },
    toFixed(startTime, endTime) {
      const start=Date.parse(new Date(startTime.replace(/[-]/g,"/")));
      const end=Date.parse(new Date(endTime.replace(/[-]/g,"/")));
      const time=(end-start)/1000/60/60;
      return time.toFixed(1)+'小时'
    },
    // 取消预约
    cancelAppmt(Id) {
      const wu = this.$Toast
      const that = this
      wu.showDialog({
        title: '',
        content: '确定取消预约？',
        showCancel: true,
        success: function(res) {
          if(res.value == "confirm") {
            axios.post('iapp/api/app/boardroom/appoint/disa/'+Id).then(res => {
              if(res.data.code == '200'){
                wu.showToast({
                  title: res.data.msg,
                  mask: false,
                  icon: 'icon-success', // icon-success | icon-error | icon-info
                  duration: 1000
                });
                if(res.data.code == 200) {
                  setTimeout(()=> {
                    that.$router.go(-1);
                  },2000)
                }
              }
            })
          }
          if(res.value == "cancel") {
            wu.showToast({
              title: '取消失败',
              duration: 1500
            })
          }
        }
      })
    },
    // 通过
    byRejecting (ID,bol) {
      const wu = this.$Toast
      const that = this
      const content = bol?'是否确定通过？':'是否拒绝？'
      wu.showDialog({
        title: '',
        content,
        showCancel: true,
        success: function(res) {
          if(res.value == "confirm") {
            axios.post(`iapp/api/app/boardroom/verify/${ID}`, {params: {audit: that.Feedback, flag: bol, t: new Date().getTime()}})
            .then( res => {
              wu.showToast({
                title: res.data.msg,
                duration: 1500,
              });
              if(res.data.code == 200) {
                setTimeout(()=> {
                  that.$router.go(-1);
                },2000)
              }
            })
          }
        }
      })
    } 
  },
  components: {
    VenueHead
  }
}
</script>
<style lang="less" scoped>
.venue_details {
  background: #f5f7fa;
  height: 100%;
  .feedback-title,
  .applicat-title {
    padding: .625rem 1.25rem;
  }
  .feedbackLists,
  .applicatLists {
    background: #fff;
    padding: 0 1.25rem;
  }
  .applicatLists {
    li {
      padding: 6px 0;
      color: #666;
      .name {
        display: inline-block;
        width: 3.875rem;
      }
      .key {
        color: #333
      }
    }
  }
  .feedbackLists {
    li {
      padding: 1.25rem 0;
      &:not(:last-child){
        border-bottom: 1px solid #e6e6e6;
      }
      .name {
        display: inline-block;
        width: 4.75rem;
      }
      .textarea {
        display: inline-block;
        vertical-align: top;
        width: 100%;
        textarea {
          outline: none;
          border: none;
          display: block;
          width: 100%;
        }
      }
    }
  }
  .footerDetails {
    position: fixed;
    bottom: 1px;
    left: 0;
    right: 0;
    padding: .625rem /* 10/16 */;
    text-align: center;
    background: #fff;
    overflow: hidden;
    display: flex;
    a {
      display: block;
      text-align: center;
      box-sizing: border-box;
      padding: .625rem /* 10/16 */ 0;
      flex: 1;
      margin: 0 .625rem /* 10/16 */;
      border-radius: .3125rem /* 5/16 */;
      &.refuseDetails {
        color: red;
        border: 1px solid red;
      }
      &.byDetails {
        background: #1f95ff;
        border: 1px solid #1f95ff;
        color: #fff;
      }
    }
  }
}
</style>



